    anime({
        targets: [target.querySelector("article")],
        translateX: translateX,
        translateY: translateY,
        duration: 400,
        easing: 'easeOutQuad'
    });
    anime({
        targets: [target.querySelector("span")],
        opacity: opacity,
        duration: 400,
        delay: 50,
        easing: 'easeOutQuad'
    });
